.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 100%;
  -webkit-justify-content: end;
          justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 980px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    border: none;
    transition:  all 0.2s ease-out;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #1888ff;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }
}
.dropdown-menu {
  background: red;
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #1888ff;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #5cabff;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}

.carousel {
    width: 100vh;
    height: 100vh;
   /* height: 700px;*/
   margin-right: 3vh;
}

.carouselInner {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: -webkit-flex;
    display: flex;
}

.carouselInner .left {
    -webkit-flex: 5% 1;
            flex: 5% 1;
    height: 100%;
    background-color: transparent;
    display: grid;
    place-items: center;
    color: #fcd7c7;
    cursor: pointer;
}

.carouselInner .center {
    -webkit-flex: 80% 1;
            flex: 80% 1;
    height: 100%;
    display: grid;
    place-items: center;
    font-family: Open Sans;
    text-align: justify;
    text-align-last: center;
}

.carouselInner .right {
    -webkit-flex: 5% 1;
            flex: 5% 1;
    height: 100%;
    background-color: transparent;
    display: grid;
    place-items: center;
    color: #fcd7c7;
    cursor: pointer;
}
.row{
	display: -webkit-flex;
	display: flex
}
.gallery{
	-webkit-column-count: 3;
	column-count: 3;
	-webkit-column-width:  33%;
	column-width:  33%;
	padding:  0 12px;
}

.gallery .pics{
	transition:  all 350ms ease;
	cursor: pointer;
	margin-bottom: 12px;
}

.gallery .pics:hover{
	background: #FFC2A8;
	-webkit-filter: opacity(.8);
	        filter: opacity(.8);
}

@media (max-width: 980px){
	.gallery{
		-webkit-column-count: 2;
		column-count: 2;
	}
}
@media (max-width: 580px){
	.gallery{
		-webkit-column-count: 1;
		column-count: 1;
		-webkit-column-width:  100%;
		column-width:  100%;
	}
}

